// app/submissions/page.tsx
import React from 'react'

type Submission = {
  rowIndex: number
  OriginalQuery: string
  Prompt: string
  ModifiedQuery: string
  Status: string
  SubmittedBy: string
  ApprovedBy: string
  Timestamp: string
  BankName: string
  Segment: string
}

async function getSubmissions(): Promise<Submission[]> {
  const res = await fetch('/api/fetch-submissions', { cache: 'no-store' })
  if (!res.ok) throw new Error('Failed to load submissions')
  return res.json()
}

export default async function SubmissionsPage() {
  const submissions = await getSubmissions()

  return (
    <div className="p-6">
      <h1 className="text-2xl font-semibold mb-4">Query Submissions</h1>
      <div className="overflow-x-auto">
        <table className="min-w-full border-collapse">
          <thead>
            <tr className="bg-gray-100">
              {[
                '#',
                'Original',
                'Prompt',
                'Modified',
                'Status',
                'By',
                'Approved By',
                'When',
                'Bank',
                'Segment',
              ].map((h) => (
                <th key={h} className="px-3 py-2 text-left text-sm font-medium">{h}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {submissions.map((s) => (
              <tr key={s.rowIndex} className="border-t">
                <td className="px-3 py-2">{s.rowIndex}</td>
                <td className="px-3 py-2">{s.OriginalQuery}</td>
                <td className="px-3 py-2">{s.Prompt}</td>
                <td className="px-3 py-2 whitespace-pre-wrap">{s.ModifiedQuery}</td>
                <td className="px-3 py-2">{s.Status}</td>
                <td className="px-3 py-2">{s.SubmittedBy}</td>
                <td className="px-3 py-2">{s.ApprovedBy}</td>
                <td className="px-3 py-2">{new Date(s.Timestamp).toLocaleString()}</td>
                <td className="px-3 py-2">{s.BankName}</td>
                <td className="px-3 py-2">{s.Segment}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  )
}
